@import url('../../../assets/Colors.css');

.EditAccount{
    width: 50%;
    margin: 2rem auto;
    display: flex;
    justify-content: space-between;
}

.EditAccount .ProfileImgDiv{
    position: relative;
}

.EditAccount .ProfileImgDiv .ImageBox{
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: initial;
}

.EditAccount .ProfileImg{
    width: 150px;
    height: 150px;
    box-shadow: 0 5px 8px rgba(0,0,0,.1);
}

.EditAccount .ProfileImgDiv label{
    margin-top: 8px;
    width: 150px;
    color: var(--themeColorDark);
    cursor: pointer;
    font-weight: 500;    
    cursor: pointer;
    font-size: 1rem;
}

.EditAccount .ProfileImgDiv input{
    visibility: hidden;
    width: 150px;
}

.EditAccount .AccountName{
    width: 100%;
    margin-bottom: 1rem;
}

.EditAccount .AccountDetails .AccountName input{
    width: 100%;
    font-size: 1.3rem;
    padding: 8px 1rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    outline: none;
}

.EditAccount .AccountDetails span{
    font-weight: 500;
    color: var(--optionColor);
}

.EditAccount .AccountDetails p{
    color: var(--optionColor);
}

.EditAccount button{
    padding: 8px 1rem;
    margin-right: 8px;
    border: none;
    background: var(--accentColor);
    outline: none;
    color: white;
    border-radius: 4px;
}

.EditAccount a{
    padding: 8px 1rem;
    border: none;
    background: var(--accentColor);
    outline: none;
    color: white;
    border-radius: 4px;
    text-decoration: none;
}

.EditAccount .AccountDetails{
    display: flex;
    flex-flow: column;
    align-items: flex-start;
}

.EditAccount .AccountDetails input{
    padding: 8px 1rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1rem;
    outline: none;
    width: 100%;
}

.EditAccount .AccountDetails textarea{
    resize: none;
    width: 100%;
    height: 100px;
    outline: none;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-bottom: 1rem;
    padding: 8px;
}

.EditAccount .AccountDetails .JoiningText{
    color: var(--optionColor);
    margin: 8px 0;
}


@media only screen and (max-width: 450px){
    .EditAccount{
        width: 90%;
        justify-content: flex-start;
    }
    .EditAccount .ProfileImgDiv{
        margin-right: 1rem;
    }

    .EditAccount .ProfileImg{
        width: 75px;
        height: 75px;
    }
}